<template>
  <div class="mobby-wallet-container">
    <balance-block
      :curr-symbol="mobbyTokenSymbol"
      :balance="balance"
      :get-balance="getBalance"
      class="balance-block"
    />
    <interface-history-tx :transactions="transactions" :tx-loaded="txLoaded" />
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import ethplorer from '@/services/ethplorer.js';
import InterfaceHistoryTx from '../../components/InterfaceHistoryTx';
import BalanceBlock from '../../components/BalanceBlock';
import { Toast } from '@/helpers';
import { BigNumber } from 'bignumber.js';

export default {
  components: {
    'interface-history-tx': InterfaceHistoryTx,
    'balance-block': BalanceBlock
  },
  props: {},
  data() {
    return {
      transactions: [],
      txLoaded: false
    };
  },
  computed: {
    ...mapGetters({
      account: 'account',
      network: 'network',
      web3: 'web3'
    }),
    mobbyTokenSymbol() {
      return this.network.type.mobbyToken.symbol;
    },
    balance() {
      return this.account.mobbyTokenBalance.toString();
    }
  },
  mounted() {
    this.getBalance();
    this.getTxs();
  },
  methods: {
    getBalance() {
      this.getTokenBalance(this.network.type.mobbyToken)
        .then(res => {
          this.$store.dispatch('setAccountMobbyTokenBalance', res);
        })
        .catch(e => {
          Toast.responseHandler(e, false);
        });
    },
    async getTokenBalance(token) {
      try {
        const web3 = this.web3;
        const contractAbi = [
          {
            name: 'balanceOf',
            type: 'function',
            constant: true,
            inputs: [{ name: 'address', type: 'address' }],
            outputs: [{ name: 'out', type: 'uint256' }]
          }
        ];
        const contract = new web3.eth.Contract(contractAbi);
        const data = contract.methods
          .balanceOf(this.account.address)
          .encodeABI();
        const balance = await web3.eth
          .call({
            to: token.address,
            data: data
          })
          .then(res => {
            let tokenBalance;
            if (Number(res) === 0 || res === '0x') {
              tokenBalance = 0;
            } else {
              const denominator = new BigNumber(10).pow(token.decimals);
              tokenBalance = new BigNumber(res).div(denominator).toString();
            }
            return tokenBalance;
          })
          .catch(e => {
            Toast.responseHandler(e, false);
          });

        return balance;
      } catch (e) {
        Toast.responseHandler(e, Toast.ERROR);
      }
    },
    getTxs() {
      ethplorer
        .getTokenTx(this.account.address)
        .then(res => {
          this.transactions = res;
          this.txLoaded = true;
        })
        .catch(e => {
          this.txLoaded = true;
          Toast.responseHandler(e, Toast.ERROR);
        });
    }
  }
};
</script>

<style lang="scss" scoped>
@import 'MobbyTokenWalletContainer.scss';
</style>
